.portfolio_container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
}

@media screen and (max-width: 1024px) {

    .portfolio_container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }

    #Portfolio {
        margin-top: 10rem;
    }
}

@media screen and (max-width: 600px) {
    .portfolio_container {
        grid-template-columns: 1fr;
        gap: 1rem;

    }

    #Portfolio {
        margin-top: 50rem;
    }


}

@media screen and (max-width: 375px) {
    #Portfolio {
        margin-top: 60rem;
    }


}